<template>
  <ion-page>
    <ion-header mode="ios">
      <ion-toolbar>
        <ion-title>Buat Jadwal Try Out KEDINASAN</ion-title>
        <ion-back-button slot="start" default-href="/" />
      </ion-toolbar>
    </ion-header>

    <ion-content :fullscreen="true">
      <ion-content class="ion-padding">
        <ion-input
          v-model="judulUjian"
          type="text"
          placeholder="Masukkan judul ujian"
          @ionInput="handleJudulInput"
        ></ion-input>

        <ion-item>
          <ion-label>Tanggal Ujian</ion-label>
          <ion-button style="color: white" @click="showTanggalPicker = true">
            {{ tanggalUjian ? formatDate(tanggalUjian) : "Pilih Tanggal" }}
          </ion-button>
          <ion-datetime
            v-if="showTanggalPicker"
            v-model="tanggalUjian"
            display-format="DD MMM YYYY"
            placeholder="Pilih Tanggal"
            @ionChange="showTanggalPicker = false"
          ></ion-datetime>
        </ion-item>

        <ion-item>
          <ion-label>Durasi Ujian (Menit)</ion-label>
          <ion-input
            v-model.number="durasiUjian"
            type="number"
            placeholder="Masukkan durasi ujian dalam menit"
            @input="logDurasiUjian"
          ></ion-input>
        </ion-item>

        <!-- Tombol untuk membuka modal memilih soal -->
        <ion-item>
          <ion-label>Pilih Soal</ion-label>
          <ion-button style="color: white" @click="showSoalModal = true">
            Pilih Soal ({{ selectedSoal.length }} soal dipilih)
          </ion-button>
        </ion-item>

        <!-- Modal Pemilihan Soal -->
        <ion-modal :is-open="showSoalModal" @did-dismiss="closeSoalModal">
          <ion-header>
            <ion-toolbar>
              <ion-title>Pilih Soal</ion-title>
              <ion-buttons slot="end">
                <ion-button @click="closeSoalModal">Tutup</ion-button>
              </ion-buttons>
            </ion-toolbar>
          </ion-header>
          <ion-content>
            <!-- Filter Jenis Soal -->
            <ion-item>
              <ion-label>Jenis Soal</ion-label>
              <ion-select v-model="filterJenisSoal" placeholder="Pilih Jenis Soal">
                <ion-select-option value="TWK">TWK</ion-select-option>
                <ion-select-option value="TKP">TKP</ion-select-option>
                <ion-select-option value="TIU">TIU</ion-select-option>
              </ion-select>
            </ion-item>

            <!-- Daftar Soal -->
            <ion-list>
              <ion-item
                v-for="soal in filteredSoal"
                :key="soal.id"
                button
                @click="toggleSelectSoal(soal)"
              >
              <ion-label>{{ removeHtmlTags(soal.question) }}</ion-label>
                <ion-checkbox
                  slot="start"
                  :checked="selectedSoal.includes(soal.id)"
                ></ion-checkbox>
              </ion-item>
            </ion-list>

            <!-- Menampilkan jumlah soal yang dipilih berdasarkan jenis -->
            <ion-item v-for="(count, jenis) in soalCountPerJenis" :key="jenis">
              <ion-label>{{ jenis }}: {{ count }} soal dipilih</ion-label>
            </ion-item>

            <ion-button expand="block" @click="closeSoalModal" style="color: white; margin-bottom: 15%">
              Tutup
            </ion-button>
          </ion-content>
        </ion-modal>

        <ion-button style="color: white" expand="block" @click="buatJadwalUjian">
          Buat Jadwal Ujian
        </ion-button>

        <ion-grid>
          <ion-row>
            <ion-col>
              <h2>Daftar Jadwal Ujian</h2>
              <ion-list>
                <ion-item>
                  <ion-label>
                    <table class="table">
                      <thead>
                        <tr>
                          <th>Tanggal</th>
                          <th>Nama Ujian</th>
                          <th>Durasi (Menit)</th>
                          <th>Soal</th>
                          <th>Aksi</th>
                        </tr>
                      </thead>
                      <tbody>
                        <tr v-for="jadwal in daftarJadwal" :key="jadwal.id">
                          <td>{{ formatDate(jadwal.tanggalUjian) }}</td>
                          <td>{{ jadwal.judulUjian }}</td>
                          <td>{{ jadwal.durasi }}</td>
                          <td>{{ jadwal.soalCount }}</td>
                          <td>
                            <ion-button
                              style="color: white"
                              @click="hapusJadwal(jadwal.id)"
                              >Hapus</ion-button
                            >
                            <ion-button
                              :color="jadwal.aktif ? 'danger' : 'success'"
                              @click="toggleAktif(jadwal.id, jadwal.aktif)"
                            >
                              {{ jadwal.aktif ? "Nonaktifkan" : "Aktifkan" }}
                            </ion-button>

                            <ion-button @click="lihatHasilUjian(jadwal.id)">Lihat Hasil Ujian</ion-button>
                          </td>
                        </tr>
                      </tbody>
                    </table>
                  </ion-label>
                </ion-item>
              </ion-list>
              <ion-button expand="block" @click="loadMore" v-if="showLoadMore">
                Load More
              </ion-button>
            </ion-col>
          </ion-row>
        </ion-grid>
      </ion-content>

      <ion-toast
        :is-open="toast.isOpen"
        :message="toast.message"
        :duration="toast.duration"
        :color="toast.color"
        @did-dismiss="() => (toast.isOpen = false)"
      ></ion-toast>
    </ion-content>
  </ion-page>
</template>

<script setup>
  import {
    IonInput,
    IonPage,
    IonHeader,
    IonToolbar,
    IonTitle,
    IonBackButton,
    IonContent,
    IonItem,
    IonLabel,
    IonDatetime,
    IonButton,
    IonGrid,
    IonRow,
    IonCol,
    IonList,
    IonToast,
    IonCheckbox,
    IonModal,
    IonSelect,
    IonSelectOption,
  } from "@ionic/vue";
  import { ref, onMounted, computed } from "vue";
  import {
    doc,
    getFirestore,
    collection,
    getDocs,
    addDoc,
    deleteDoc,
    updateDoc,
    query,
    orderBy,
  } from "firebase/firestore";
  import { firebaseApp } from "../firebase";
  import { useRouter } from "vue-router";
  const router = useRouter();
  const lihatHasilUjian = (jadwalId) => {
  router.push({ name: 'HasilUjianKedinasan', params: { jadwalId } });
};
  const db = getFirestore(firebaseApp);

  const tanggalUjian = ref("");
  const durasiUjian = ref(0);
  const selectedSoal = ref([]); // Menyimpan soal yang dipilih
  const daftarSoal = ref([]);
  const daftarJadwal = ref([]);
  const showTanggalPicker = ref(false);
  const judulUjian = ref("");
  const showSoalModal = ref(false); // Modal untuk memilih soal
  const filterJenisSoal = ref(""); // Untuk filter jenis soal
  
  const removeHtmlTags = (html) => {
  const doc = new DOMParser().parseFromString(html, "text/html");
  return doc.body.textContent || "";
  };



  const toast = ref({
    isOpen: false,
    message: "",
    duration: 2000,
    color: "success",
  });

  const fetchSoal = async () => {
    try {
      const querySnapshot = await getDocs(collection(db, "soalkedinasan"));
      daftarSoal.value = querySnapshot.docs.map((doc) => ({
        id: doc.id,
        ...doc.data(),
      }));
    } catch (error) {
      console.error("Error fetching soal:", error);
    }
  };

  const fetchJadwal = async () => {
    try {
      const querySnapshot = await getDocs(
        query(collection(db, "jadwalkedinasan"), orderBy("tanggalUjian"))
      );
      daftarJadwal.value = querySnapshot.docs.map((doc) => ({
        id: doc.id,
        ...doc.data(),
        soalCount: doc.data().soal.length,
      }));
    } catch (error) {
      console.error("Error fetching jadwal:", error);
      showToast("Terjadi kesalahan saat mengambil data jadwal.", "danger");
    }
  };

  const logDurasiUjian = (event) => {
    durasiUjian.value = parseInt(event.target.value); // Convert to integer
  };

  const buatJadwalUjian = async () => {
    if (
      !judulUjian.value ||
      !tanggalUjian.value ||
      durasiUjian.value <= 0 ||
      selectedSoal.value.length === 0
    ) {
      showToast("Harap lengkapi semua field.", "danger");
      return;
    }

    try {
      await addDoc(collection(db, "jadwalkedinasan"), {
        judulUjian: judulUjian.value,
        tanggalUjian: tanggalUjian.value,
        durasi: durasiUjian.value,
        soal: selectedSoal.value,
        aktif: true,
      });
      showToast("Jadwal ujian berhasil dibuat.", "success");
      resetForm();
      fetchJadwal();
    } catch (error) {
      console.error("Error creating jadwal:", error);
      showToast("Terjadi kesalahan saat membuat jadwal ujian.", "danger");
    }
  };

  const resetForm = () => {
    judulUjian.value = "";
    tanggalUjian.value = "";
    durasiUjian.value = 0;
    selectedSoal.value = [];
  };

  const handleJudulInput = (event) => {
    console.log("Judul Ujian input event:", event.target.value);
  };

  const hapusJadwal = async (jadwalId) => {
    try {
      await deleteDoc(doc(db, "jadwalkedinasan", jadwalId));
      showToast("Jadwal ujian berhasil dihapus.", "success");
      fetchJadwal();
    } catch (error) {
      console.error("Error deleting jadwal:", error);
      showToast("Terjadi kesalahan saat menghapus jadwal ujian.", "danger");
    }
  };

  const toggleAktif = async (jadwalId, isActive) => {
    try {
      await updateDoc(doc(db, "jadwalkedinasan", jadwalId), {
        aktif: !isActive,
      });
      showToast("Status jadwal ujian berhasil diubah.", "success");
      fetchJadwal();
    } catch (error) {
      console.error("Error updating jadwal status:", error);
      showToast("Terjadi kesalahan saat mengubah status jadwal.", "danger");
    }
  };

  const formatDate = (date) => {
    if (!date) return "N/A";
    const d = new Date(date);
    return d.toLocaleDateString("id-ID", {
      year: "numeric",
      month: "short",
      day: "numeric",
    });
  };

  // Fungsi untuk menampilkan toast
  const showToast = (message, color) => {
    toast.value = {
      isOpen: true,
      message,
      duration: 2000,
      color,
    };
  };

  // Menambahkan soal ke dalam pilihan
  const toggleSelectSoal = (soal) => {
    if (selectedSoal.value.includes(soal.id)) {
      selectedSoal.value = selectedSoal.value.filter(id => id !== soal.id); 
    } else {
      selectedSoal.value.push(soal.id);
    }
  };

  // Menutup modal soal
  const closeSoalModal = () => {
    showSoalModal.value = false;
  };

  // Filter soal berdasarkan jenis
  const filteredSoal = computed(() => {
    if (!filterJenisSoal.value) return daftarSoal.value;
    return daftarSoal.value.filter((soal) => soal.jenissoal === filterJenisSoal.value);
  });

  // Menghitung jumlah soal per jenis
  const soalCountPerJenis = computed(() => {
    const counts = {};
    selectedSoal.value.forEach(soalId => {
      const soal = daftarSoal.value.find(s => s.id === soalId);
      if (soal) {
        const jenis = soal.jenissoal;
        counts[jenis] = counts[jenis] ? counts[jenis] + 1 : 1;
      }
    });
    return counts;
  });

  onMounted(() => {
    fetchSoal();
    fetchJadwal();
  });
</script>

<style scoped>
.table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 20px;
}

.table th,
.table td {
  border: 1px solid #ddd;
  padding: 8px;
  text-align: left;
}

.table th {
  background-color: #f2f2f2;
}

.table tbody tr:hover {
  background-color: #f1f1f1;
}
</style>
